
import { todoStore } from '@/providers/todoStore';
import { defineComponent, ref } from 'vue';

export default defineComponent({
  name: 'app-input',
  setup() {
    const { addTodo } = todoStore;
    const content = ref('');

    const handleSubmit = () => {
      addTodo(content.value);
      content.value = '';
    };

    return {
      content,
      handleSubmit,
    };
  },
});
